.sectionHeader {
    background-color: hsl(34, 10%, 14%);
    -webkit-box-shadow: 0px 1px 4px 2px hsl(36, 9%, 10%); 
    box-shadow: 0px 1px 4px 2px hsl(36, 9%, 10%);
    color: hsl(15, 10%, 92%);
    display: flex;
    padding: 1rem 2rem;
}

.logoWrapper {
    color: inherit;
    text-decoration: none
}

.logo {
    color: var(--primary-300);
    font-size: 1.4rem;
    font-weight: 800;
    margin: 0;
    overflow: hidden;
}

.logo span {
    letter-spacing: -30px;
    opacity: 0;
    transition: letter-spacing 0.2s ease-in-out, opacity 0.3s ease;

}

.logo:hover span {
    letter-spacing: initial;
    opacity: 1;
}



